import React, {Component} from 'react'
import cx from 'classnames'
import Draggable from 'react-draggable'
import './Card.css'

class Card extends Component {
  constructor(props) {
    super(props)
    this.myRef = React.createRef()
  }

  state = {
    position: {
      x: 0,
      y: 0,
    },
  }

  componentDidMount() {
    this.innerWidth = window.innerWidth
  }

  handleDragStart = () => {}

  handleDragStop = (e, data) => {
    console.log(e)
    console.log(data)
    const {onSlideSuccess, onStartAni} = this.props
    console.log(onSlideSuccess)
    const {lastX} = data
    this.myRef.current.style.transition = 'transform 300ms'
    if (lastX < -120) {
      onStartAni(true)
      this.setState({
        position: {
          x: -this.innerWidth,
          y: 0,
        },
      })
    }
    setTimeout(() => {
      onStartAni(false)
      this.myRef.current.style.transition = 'transform 0ms'
      if (lastX < -120) {
        onSlideSuccess()
      }
    }, 300)
  }

  draggableConfig = {
    axis: 'x',
    onStart: this.handleDragStart,
    onStop: this.handleDragStop,
    bounds: {
      right: 120,
    },
  }

  render() {
    const {title, desc} = this.props.content
    const {className} = this.props
    return (
      <div className={cx('Card-wrapper', className)}>
        <Draggable {...this.draggableConfig} position={this.state.position}>
          <section className={cx('Card', className)} ref={this.myRef}>
            <h2 className="CardTitle">{title}</h2>
            <p className="CardDesc">
              {desc}
              ...
            </p>
            <button>忽略</button>
            <button>回答</button>
          </section>
        </Draggable>
      </div>
    )
  }
}

export default Card



