import React, {Component} from 'react'
import cx from 'classnames'
import Card from './Components/Card'
import './App.css'

class App extends Component {
  state = {
    data: [
      {
        title: '行宫1',
        desc: '寥落古行宫，宫花寂寞红。白头宫女在，闲坐说玄宗。',
        id: 1213,
      },
      {
        title: '送别 / 山中送别2',
        desc: '山中相送罢，日暮掩柴扉。春草明年绿，王孙归不归？',
        id: 2213,
      },
      {
        title: '渡汉江3',
        desc: '岭外音书断，经冬复历春。近乡情更怯，不敢问来人。',
        id: 3123,
      },
    ],
    startAni: false,
  }

  handleSlideSuccess = () => {
    this.setState({
      data: this.state.data.slice(1),
    })
  }

  handleStartAni = status => {
    this.setState({
      startAni: status,
    })
  }

  render() {
    const {data, startAni} = this.state
    return (
      <div className="App">
        {data
          .slice(0, 2)
          .reverse()
          .map((value, index) => (
            <Card
              content={value}
              key={value.id}
              className={cx({
                'Card-bottom': index === 0,
                'Card-top': startAni,
              })}
              onSlideSuccess={this.handleSlideSuccess}
              onStartAni={this.handleStartAni}
            />
          ))}
      </div>
    )
  }
}

export default App



